.side-menu {
  background: red; }
  .side-menu__toggle {
    position: fixed;
    top: 50px;
    left: 50px;
    z-index: 100;
    height: 50px;
    width: 50px;
    cursor: pointer;
    transition-duration: 0.3s; }
    .side-menu__toggle:after, .side-menu__toggle:before {
      content: "";
      background: black;
      height: 2px;
      width: 50px;
      position: absolute;
      top: 15px;
      transition-duration: 0.3s; }
    .side-menu__toggle:before {
      bottom: 15px;
      top: auto; }
    .side-menu__toggle.is-active:after, .side-menu__toggle.is-active:before {
      top: 50%;
      bottom: auto;
      transform: rotate(-45deg); }
    .side-menu__toggle.is-active:before {
      transform: rotate(45deg); }
    @media screen and (max-width: 599px) {
      .side-menu__toggle.is-active {
        top: 10px;
        right: 10px;
        left: auto;
        height: 30px;
        width: 30px; }
        .side-menu__toggle.is-active:after, .side-menu__toggle.is-active::before {
          width: 100%; } }
    .side-menu__toggle.style-black:after, .side-menu__toggle.style-black:before {
      background: white; }
    @media screen and (max-width: 1024px) {
      .side-menu__toggle {
        top: 20px;
        left: 20px; } }
  .side-menu__bg {
    left: -100vw;
    top: 0;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: transparent;
    z-index: 98;
    transition-duration: 1s;
    transition-property: opacity; }
    .side-menu__bg.is-active {
      left: 0;
      background: rgba(0, 0, 0, 0.6); }
  .side-menu__nav {
    position: fixed;
    top: 0;
    left: -600px;
    padding: 0 0 0 200px;
    height: 100vh;
    z-index: 99;
    background: white;
    width: 600px;
    display: flex;
    transition-duration: 0.3s;
    overflow-y: auto; }
    .side-menu__nav.is-active {
      left: 0; }
    @media screen and (max-width: 599px) {
      .side-menu__nav {
        padding: 30px; } }

.s-menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100%; }
  @media screen and (max-height: 600px) {
    .s-menu {
      display: block;
      min-height: 0; } }
  .s-menu__wrap {
    padding: 20px 0; }
  .s-menu__item {
    list-style: none;
    margin-bottom: 30px; }
  .s-menu__link {
    padding: 0;
    margin: 0;
    font-size: 16px;
    color: black;
    font-family: "Circe";
    transition-duration: 0.3s; }
    .s-menu__link:hover, .s-menu__link.is-active {
      font-weight: 700; }
